@function rem($size) {
  $remSize: $size / 16px;
  @return ($remSize) * 1rem;
}

@function em($pixels, $context: 16px) {
  @return ($pixels/$context) * 1em;
}


//returns a color by calculating the lightness of the passed color.
@function set-color($color,$clamp) {
    @if (lightness($color) < $clamp) {
      @return $clr1;
    }
    @else {
      @return $clr5;
    }
}
